body {
    background-color: black;
    
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 50px;

}

.calc-wrapper {
    background-color: grey;
    width: 400px;
    border-radius: 30px;
    font-size: 18px;
}

.row {
    display: flex;
}

@media(max-width: 832px){ 
    body {
        background-color: black;
        
    }
    
    .App {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 50px;
    
    }
    
    .calc-wrapper {
        background-color: grey;
        width: 400px;
        border-radius: 30px;
    }
    
    .row {
        display: flex;
    }
    
  }